import { useState } from "react";
import { Result } from "./Result";
import { Clock } from "./Clock";
import {
  Legend,
  Paragraph,
  Label,
  Strong,
  Input,
  Wrapper,
  Button,
  ErrorText,
  LoadingText,
  InfoText,
} from "./styled";
import { useRatesFromApi } from "../useRatesFromApi";

export const Form = () => {
  const [amount, setAmount] = useState("");
  const [result, setResult] = useState();
  const [currency, setCurrency] = useState("EUR");
  const ratesData = useRatesFromApi();

  const calculateResult = () => {
    const rate = ratesData.rates[currency];

    setResult({
      sourceAmount: +amount,
      targetAmount: amount * rate,
      currency,
    });
  };

  const onFormSubmit = (event) => {
    event.preventDefault();
    calculateResult(amount, currency);
  };

  const onFormReset = () => {
    setAmount("");
    setCurrency();
    setResult("");
  };

  return (
    <form onSubmit={onFormSubmit} onReset={onFormReset}>
      <Clock />
      <Legend>Kalkulator walut</Legend>
      {ratesData.state === "loading" ? (
        <LoadingText>
          Trwa adowanie kursw walut z Europejskiego Banku Centralnego...
        </LoadingText>
      ) : ratesData.state !== "success" ? (
        <ErrorText>
          Bd ! Co poszo nie tak.
          <br />
          Sprawd, czy masz poczenie z internetem. <br />
          Jeli tak, wina ley po stronie serwera. <br />
          Przepraszamy !
        </ErrorText>
      ) : (
        <>
          <Paragraph>Wymagane pola oznaczone s gwiazdk*</Paragraph>
          <Label>
            <Strong>Kwota (z):*</Strong>
            <Input
              type="number"
              value={amount}
              onChange={({ target }) => setAmount(target.value)}
              name="amount"
              min="1"
              step="any"
              placeholder="Wpisz kwot..."
              required
            />
          </Label>
          <Label>
            <Strong>Waluta:*</Strong>
            <Input
              as="select"
              name="currency"
              value={currency}
              onChange={({ target }) => setCurrency(target.value)}
            >
              {Object.keys(ratesData.rates).map((currency) => (
                <option value={currency} key={currency}>
                  {currency}
                </option>
              ))}
            </Input>
          </Label>
          <Wrapper>
            <Button type="submit">Przelicz</Button>
            <Button type="reset">Wyczy</Button>
          </Wrapper>
          <Result result={result} />
          <InfoText>
            Kursy walut pobierane s z Europejskiego Banku Centralnego. <br />
            Aktualne na dzie: <strong>{ratesData.date}</strong>
          </InfoText>
        </>
      )}{" "}
    </form>
  );
};
